import React, { Component } from 'react'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Badge from '@material-ui/core/Badge'
import Typography from '@material-ui/core/Typography'
import pink from '@material-ui/core/colors/pink'
import green from '@material-ui/core/colors/green'

const styles = {
    card: {
        margin: 24,
        marginTop: 0,
        overflow: 'visible',
        cursor: 'pointer',
    },
    badge: {
        position: 'absolute',
        top: 0,
        left: 0,
    },
    typo: (option) => ({
        display: 'inline-block',
        margin: '0 12px',
        fontSize: option === 'detail' ? 12 : 16,
        lineHeight: '23px',
        fontWeight: option === 'time' ? 'bold' : 'normal',
        width: option === 'detail' ? 200 : (option === 'station' ? 250 : null),
        verticalAlign: 'middle',
    }),
    content: (over, selected, isTGVMax) => ({
        background: over ? pink[100] : (selected ? pink[50] : (isTGVMax ? green[50] : null)),
        padding: 12,
        position: 'relative',
    })
};

class Train extends Component {
    state = {
        over: false,
    };

    handleMouse = (value) => () => {
        !this.props.noOver && this.setState({ over: value });
    };

    toggleTrain = () => {
        this.props.toggle && this.props.toggle(this.props.train);
    };

    findStationLabel = (stationCode) => {
        return this.props.stations[stationCode] ? this.props.stations[stationCode].stationLabel : stationCode;
    };

    render() {
        const departure = this.props.train.departureDate.split('T')[1].substring(0, 5);
        const arrival = this.props.train.arrivalDate.split('T')[1].substring(0, 5);
        const origin = this.findStationLabel(this.props.train.originStationCode);
        const destination = this.findStationLabel(this.props.train.destinationStationCode);
        const duration = parseInt(this.props.train.minuteDuration / 60, 10) + 'h' + ('0' + this.props.train.minuteDuration % 60).slice(-2);
        const transporters = this.props.train.transporters.join(', ');
        const isTGVMax = this.props.train.priceProposals.reduce((acc, price) => acc || price.amount === 0, false);

        return (
            <Card
                style={styles.card}
                onMouseEnter={this.handleMouse(true)}
                onMouseLeave={this.handleMouse(false)}
                onClick={this.toggleTrain}
            >
                <CardContent style={styles.content(this.state.over, this.props.index > 0, isTGVMax)}>
                    {this.props.index ? <Badge badgeContent={this.props.index} color="secondary" style={styles.badge}>&nbsp;</Badge> : null}
                    <Typography style={styles.typo('time')}>{departure}<br />{arrival}</Typography>
                    <Typography style={styles.typo('station')}>{origin}<br />{destination}</Typography>
                    <Typography style={styles.typo('detail')}>{duration}<br />{transporters}</Typography>
                </CardContent>
            </Card>
        );
    }
}

export default Train


