import React, { Component } from 'react'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import Paper from '@material-ui/core/Paper'
import TextField from '@material-ui/core/TextField'
import NativeSelect from '@material-ui/core/NativeSelect'
import Button from '@material-ui/core/Button'
import FormControl from '@material-ui/core/FormControl'
import InputLabel from '@material-ui/core/InputLabel'
import MenuItem from '@material-ui/core/MenuItem'
import CircularProgress from '@material-ui/core/CircularProgress'
import Snackbar from '@material-ui/core/Snackbar'
import Autosuggest from 'react-autosuggest'
import { post, get, CancelToken } from 'axios'
import red from '@material-ui/core/colors/red'

import outward from './json/outward'
import getBookingInfo from './json/getBookingInfo'
import Trains from './Trains'
import Result from './Result'
import Utils from './Utils'

const styles = {
    content: {
        margin: 24,
        padding: 24,
    },
    control: {
        marginRight: 12,
        width: 160,
    },
    button: {
        marginRight: 12,
    },
    loading: {
        width: 48,
        height: 48,
        marginTop: 24,
        marginLeft: 'calc(50% - 48px)',
    },
    snackbar: {
        background: red[900],
    },
};

const classes = {
    container: {
      position: 'relative',
      display: 'inline-block',
    },
    suggestionsContainerOpen: {
      position: 'absolute',
      zIndex: 1,
      left: 0,
      right: 0,
    },
    suggestion: {
      display: 'block',
    },
    suggestionsList: {
      margin: 0,
      padding: 0,
      listStyleType: 'none',
    },
    divider: {
    },
  };

const times = [...Array(24).keys() ];

let source;

class App extends Component {
    state = {
        date: '',
        card: '',
        birth: '',
        time: '',
        origin: '',
        destination: '',
        seat: 'ANY',
        deck: 'ANY',
        trains: [],
        fares: [],
        query: {},
        stations: {},
        selected: [],
        loading: false,
        analysis: false,
        result: null,
        _origin: '',
        _origins: [],
        _destination: '',
        _destinations: [],
        snackbar: false,
    };

    componentDidMount = () => {
        let date = new Date();
        date.setDate(date.getDate() + (date.getHours() < 12 ? 30 : 31));

        this.setState({
            time: 18,
            date: Utils.formatDate(date),
            card: localStorage.getItem('card') || '',
            birth: localStorage.getItem('birth') || '',
            deck: localStorage.getItem('deck') || 'ANY',
            seat: localStorage.getItem('seat') || 'ANY',
        });
    };

    handleChange = (field) => (event) => {
        this.setState({ [field]: event.target.value });

        if (field === 'card' || field === 'birth' || field === 'seat' || field === 'deck') {
            localStorage.setItem(field, event.target.value);
        }
    };

    searchTrains = () => {
        this.refreshTrains();
        this.setState({ loading: true, selected: [] });
    };

    refreshTrains = () => {
        const departure = Utils.formatDateTime(this.state.date, this.state.time);
        const birthDate = Utils.formatDateTime(this.state.birth, 0);
        const query = outward(this.state.origin, this.state.destination, departure, this.state.card, birthDate);
        post('https://www.oui.sncf/proposition/rest/search-travels/outward', query)
        .then(response => {
            const trains = response.data.trainProposals || [];
            this.setState({
                query,
                trains,
                fares: response.data.fareInformations || [],
                stations: response.data.detailedLocalities || {},
                loading: false,
            });

            if (this.state.selected.length > 0) {
                let trainsObj = {};
                for (let train of trains) {
                    trainsObj[train.id] = train;
                }

                this.setState({ selected: this.state.selected.map(train => trainsObj[train.id]) });
            }
        })
        .catch(e => {
            if (!this.state.analysis) {
                this.setState({ loading: false, snackbar: true, trains: [], selected: [] });
            }
        })
        .finally(() => {
            if (this.state.analysis) {
                this.cyclicAnalysis();
            }
        });
    };

    searchTGVMaxPrice = (train) => {
        const prices = train.priceProposals || [];
        for (let price of prices) {
            if (price.amount === 0) {
                return price;
            }
        }

        return null;
    };

    searchTGVMaxTrain = () => {
        let priceTGVMax;
        for (let train of this.state.selected) {
            priceTGVMax = this.searchTGVMaxPrice(train);

            if (priceTGVMax) {
                return train;
            }
        }

        return null;
    };

    toggleTrain = (train) => {
        const index = this.state.selected.indexOf(train);
        let selected = this.state.selected;

        if (index >= 0) {
            selected.splice(index, 1);
        } else {
            selected.push(train);
        }

        this.setState({ selected });
    };

    bookTrain = (train) => {
        const price = this.searchTGVMaxPrice(train);
        const input = getBookingInfo(train, price, this.state.fares, this.state.query, this.state.deck, this.state.seat);

        post('https://www.oui.sncf/proposition/rest/get-booking-info', input).then(response => {
            const body = document.getElementsByTagName('body')[0];
            let form = document.createElement('form');
            form.setAttribute('method', 'post');
            form.setAttribute('action', response.data.url);

            const parameters = response.data.parameters || [];
            for (let key in parameters) {
                let field = document.createElement('input');
                field.setAttribute('type', 'hidden');
                field.setAttribute('name', key);
                field.setAttribute('value', response.data.parameters[key]);
                form.appendChild(field);
            }

            body.appendChild(form);
            form.submit();
        });

        this.setState({ selected: [], trains: [], analysis: false, result: train });
    };

    cyclicAnalysis = () => {
        const train = this.searchTGVMaxTrain();
        if (train) {
            this.bookTrain(train);
        } else {
            setTimeout(() => this.refreshTrains(), 5000);
        }
    };

    toggleAnalysis = () => {
        const analysis = !this.state.analysis;
        this.setState({ analysis });

        if (analysis) {
            this.cyclicAnalysis();
        }
    };

    getSuggestionValue = (suggestion) => suggestion.label;

    renderSuggestion = (suggestion, { isHighlighted }) => {
        return (
            <MenuItem selected={isHighlighted} component="div" style={{fontSize:12, padding: 6 }}>{suggestion.label}</MenuItem>
        )
    };

    onSuggestionsFetchRequested = field => ({ value }) => {
        if (source) {
            source.cancel();
        }

        source = CancelToken.source();
        get(`https://www.oui.sncf/proposition/rest/autocomplete/stations?searchTerm=${value.trim()}`, {
            cancelToken: source.token,
        }).then(response => {
            this.setState({ [field]: response.data });
            source = null;
        }).catch(e => {});
    };
    
    onSuggestionsClearRequested = field => () => {
        this.setState({ [field]: [] });
    };

    handleChangeSuggest = (fieldValue, fieldCode) => (event, { newValue }) => {
        this.setState({ [fieldValue]: newValue, [fieldCode]: '' });
    };

    renderInputComponent = (inputProps) => {
        return (
          <TextField
            {...inputProps}
            innerRef={inputProps.ref}
            ref={null}
          />
        );
      };

    renderSuggestionsContainer = (options) => (
        <Paper {...options.containerProps} square>
            {options.children}
        </Paper>);

    shouldRenderSuggestions = (value) => {
        return value.trim().length > 2;
    };

    onSuggestionSelected = (field) => (event, { suggestion }) => {
        this.setState({ [field]: suggestion.code });
    };

    handleCloseSnackbar = () => {
        this.setState({ snackbar: false });
    };

    render() {
        return (
            <div>
                <AppBar position="static" color="primary">
                    <Toolbar>
                        <Typography variant="title" color="inherit">
                            TGVMax Booking
                        </Typography>
                    </Toolbar>
                </AppBar>
                
                <Paper style={styles.content}>
                    <TextField
                        label="Code TGVMax"
                        placeholder="HC..."
                        InputLabelProps={{ shrink: true }}
                        onChange={this.handleChange('card')}
                        value={this.state.card}
                        style={styles.control}
                        disabled={this.state.analysis}
                    />
                    <TextField
                        label="Date de naissance"
                        type="date"
                        InputLabelProps={{ shrink: true }}
                        onChange={this.handleChange('birth')}
                        value={this.state.birth}
                        style={styles.control}
                        disabled={this.state.analysis}
                    />
                    <FormControl
                        style={styles.control}
                        disabled={this.state.analysis}
                    >
                        <InputLabel>Salle par défaut</InputLabel>
                        <NativeSelect
                            value={this.state.deck}
                            onChange={this.handleChange('deck')}
                            disabled={this.state.analysis}
                        >
                            <option value="ANY">Indifférent</option>
                            <option value="HAUT">Haute</option>
                            <option value="BAS">Basse</option>
                        </NativeSelect>
                    </FormControl>
                    <FormControl
                        style={styles.control}
                        disabled={this.state.analysis}
                    >
                        <InputLabel>Place par défaut</InputLabel>
                        <NativeSelect
                            value={this.state.seat}
                            onChange={this.handleChange('seat')}
                            disabled={this.state.analysis}
                        >
                            <option value="ANY">Indifférent</option>
                            <option value="FEN">Fenêtre</option>
                            <option value="COU">Couloir</option>
                            <option value="DUO_COTE_A_COTE">Duo côte à côte</option>
                        </NativeSelect>
                    </FormControl>
                    <br />
                    <br />

                    <Autosuggest
                        suggestions={this.state._origins}
                        onSuggestionsFetchRequested={this.onSuggestionsFetchRequested('_origins')}
                        onSuggestionsClearRequested={this.onSuggestionsClearRequested('_origins')}
                        getSuggestionValue={this.getSuggestionValue}
                        renderSuggestion={this.renderSuggestion}
                        renderInputComponent={this.renderInputComponent}
                        renderSuggestionsContainer={this.renderSuggestionsContainer}
                        shouldRenderSuggestions={this.shouldRenderSuggestions}
                        onSuggestionSelected={this.onSuggestionSelected('origin')}
                        focusInputOnSuggestionClick={false}
                        inputProps={{
                            value: this.state._origin,
                            onChange: this.handleChangeSuggest('_origin', 'origin'),
                            label: 'Depuis',
                            disabled: this.state.analysis,
                            InputLabelProps: { shrink: true },
                        }}
                        theme={{
                            container: classes.container,
                            input: styles.control,
                            suggestionsContainerOpen: classes.suggestionsContainerOpen,
                            suggestionsList: classes.suggestionsList,
                            suggestion: classes.suggestion,
                        }}
                    />
                    <Autosuggest
                        suggestions={this.state._destinations}
                        onSuggestionsFetchRequested={this.onSuggestionsFetchRequested('_destinations')}
                        onSuggestionsClearRequested={this.onSuggestionsClearRequested('_destinations')}
                        getSuggestionValue={this.getSuggestionValue}
                        renderSuggestion={this.renderSuggestion}
                        renderInputComponent={this.renderInputComponent}
                        renderSuggestionsContainer={this.renderSuggestionsContainer}
                        shouldRenderSuggestions={this.shouldRenderSuggestions}
                        onSuggestionSelected={this.onSuggestionSelected('destination')}
                        focusInputOnSuggestionClick={false}
                        inputProps={{
                            value: this.state._destination,
                            onChange: this.handleChangeSuggest('_destination', 'destination'),
                            label: 'Vers',
                            disabled: this.state.analysis,
                            InputLabelProps: { shrink: true },
                        }}
                        theme={{
                            container: classes.container,
                            input: styles.control,
                            suggestionsContainerOpen: classes.suggestionsContainerOpen,
                            suggestionsList: classes.suggestionsList,
                            suggestion: classes.suggestion,
                        }}
                    />
                    <TextField
                        label="Date"
                        type="date"
                        InputLabelProps={{ shrink: true }}
                        onChange={this.handleChange('date')}
                        value={this.state.date}
                        style={styles.control}
                        disabled={this.state.analysis}
                    />
                    <FormControl
                        style={styles.control}
                        disabled={this.state.analysis}
                    >
                        <InputLabel>Créneau</InputLabel>
                        <NativeSelect
                            value={this.state.time}
                            onChange={this.handleChange('time')}
                        >
                            {times.map(time => <option value={time} key={time}>{time}h</option>)}
                        </NativeSelect>
                    </FormControl>
                    <br />
                    <br />

                    <Button
                        variant="contained" 
                        color="primary"
                        style={styles.button}
                        disabled={this.state.loading || this.state.analysis || !this.state.card || !this.state.birth || !this.state.origin || !this.state.destination}
                        onClick={this.searchTrains}
                    >Rechercher les trains</Button>
                    <Button
                        variant="contained"
                        color="secondary"
                        disabled={this.state.selected.length <= 0}
                        style={styles.button}
                        onClick={this.toggleAnalysis}
                    >{this.state.analysis ? 'Stopper' : 'Lancer'} l'analyse</Button>
                </Paper>

                {!this.state.loading && <Trains
                    analysis={this.state.analysis}
                    trains={this.state.trains}
                    stations={this.state.stations}
                    selected={this.state.selected}
                    toggleTrain={this.toggleTrain}
                />}

                {this.state.loading || this.state.analysis ? 
                    <CircularProgress
                        style={styles.loading}
                        color={this.state.loading ? 'primary' : 'secondary'}
                    /> : 
                    null
                }

                {this.state.result && <Result
                    train={this.state.result}
                    stations={this.state.stations}
                />}

                <Snackbar
                    ContentProps={{ style: styles.snackbar }}
                    autoHideDuration={4000}
                    message="Une erreur s'est produite lors de la recherche des trains"
                    open={this.state.snackbar}
                    onClose={this.handleCloseSnackbar}
                />
            </div>
        );
    }
}

export default App



