import React, { Component } from 'react'

import Train from './Train'

class Trains extends Component {
    render() {
        return (
            <div>
                {this.props.trains.map(train => {
                    const index = this.props.selected.indexOf(train) + 1;
                    return (this.props.analysis && !index ? null : <Train 
                        train={train} 
                        key={train.id}
                        index={index}
                        stations={this.props.stations}
                        toggle={this.props.analysis ? null : this.props.toggleTrain}
                    />);
                })}
            </div>
        );
    }
}

export default Trains


