class Utils {
    static twoDigits = (number) => {
        return ('0' + number).slice(-2);
    };

    static formatDate = (date) => {
        const year = date.getFullYear();
        const month = Utils.twoDigits(date.getMonth() + 1);
        const day = Utils.twoDigits(date.getDate());
        return `${year}-${month}-${day}`;
    };

    static formatDateTime = (string, time) => {
        const date = new Date(string);
        const hour = Utils.twoDigits(time);
        return `${Utils.formatDate(date)}T${hour}:00:00`;
    };
}

export default Utils


