const placementSelection = (segment, defaultDeck, defaultSeat) => ({
    "segmentId": segment ? segment.segmentId : null,
    "type": "DEFAULT",
    "mode": "SELECTION",
    "seat": segment && segment.placementOptions && segment.placementOptions.seats && segment.placementOptions.seats.indexOf(defaultSeat) >= 0 ? defaultSeat : "ANY",
    "deck": segment && segment.placementOptions && segment.placementOptions.decks && segment.placementOptions.decks.indexOf(defaultDeck) >= 0 ? defaultDeck : "ANY",
    "comfortSpace": {
        "value": "ANY",
        "code": null,
        "type": null,
        "name": null,
        "description": null,
        "price": 0,
        "currency": null
    },
    "berthLevels": null,
    "womenOnlyBerth": false,
    "openPlacement": false,
    "sex": null
});

const getBookingInfo = (train, price, fareInformations, query, defaultDeck, defaultSeat) => ({
    "outwardSelection": {
        "trainProposal": train,
        "priceId": price ? price.id : null,
        "fareInformations": fareInformations,
        "placementSelections": price && price.segmentProposals ? price.segmentProposals.map(segment => placementSelection(segment, defaultDeck, defaultSeat)) : [],
        "detailedLocalities": {},
        "query": query
    },
    "inwardSelection": null,
    "travelQuery": query,
    "currencyRate": {
        "currency": "EUR",
        "rate": 1
    }
});

export default getBookingInfo


