import React, { Component } from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import Typography from '@material-ui/core/Typography'
import CircularProgress from '@material-ui/core/CircularProgress'
import green from '@material-ui/core/colors/green'

import Train from './Train';

const styles = {
    dialog: {
        maxWidth: 700,
    },
    waiting: {
        width: 48,
        height: 48,
        marginLeft: 'calc(50% - 48px)',
        color: green[500],
    },
    typo: {
        marginBottom: 24,
    },
};

class Result extends Component {
    render() {
        return (
            <Dialog open={true} PaperProps={{ style: styles.dialog }}>
                <DialogTitle>Un billet va être ajouté dans le panier ...</DialogTitle>
                <DialogContent>
                    <Typography style={styles.typo}>
                        Une redirection est en cours pour ajouter ce billet dans le panier SNCF.
                    </Typography>
                    <Train
                        train={this.props.train}
                        stations={this.props.stations}
                        toggle={null}
                        noOver={true}
                    />
                    <CircularProgress style={styles.waiting} />
                </DialogContent>
            </Dialog>
        );
    }
}

export default Result


